import * as React from 'react';
import * as ReactDOM from 'react-dom';
import './style.css';
import { Provider } from 'react-redux';
import { createBrowserHistory } from 'history';
import  configureStore  from 'app/store';
// import { Router } from 'react-router';
import { App } from './app';
import '!style-loader!css-loader!bootstrap/dist/css/bootstrap.css';
import '!style-loader!css-loader!react-input-range/lib/css/index.css';
import '!style-loader!css-loader!react-image-gallery/styles/css/image-gallery.css';
import '!style-loader!css-loader!slick-carousel/slick/slick.css';
import '!style-loader!css-loader!slick-carousel/slick/slick-theme.css';





import { library } from '@fortawesome/fontawesome-svg-core'

import {fas,  faIgloo, faSearch, faPhone, faEnvelope, faHeart, faShoppingCart, faStar, faSearchPlus, faArrowDown, faArrowUp,
        faAngleDown, faAngleUp, faCheckCircle
} from '@fortawesome/free-solid-svg-icons'

import { fab, faEbay } from '@fortawesome/free-brands-svg-icons'

import { ConnectedRouter } from 'connected-react-router';

library.add(fas, fab, faIgloo, faCheckCircle, faSearch, faPhone, faEnvelope, faHeart, faShoppingCart, faStar, faSearchPlus, faArrowDown, faArrowUp, faAngleDown, faAngleUp, faEbay)


// prepare store
const history = createBrowserHistory();
const store = configureStore();
ReactDOM.render(
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <App />
    </ConnectedRouter>
  </Provider>,
  document.getElementById('root')
);

