import { RootState } from "./state";
import { SearchActions } from "app/actions/searchActions";
import { handleActions } from "redux-actions";
import { ShoppingCategories, SearchModel } from "app/utils/consts";


const initialState: RootState.SearchState = {
    searchStatus: SearchModel as any,
    searchCategory: ShoppingCategories.AllCategories as any,
    searchStores: [] as any,
    searchText: '' as any,
    minimumPrice: -1 as any,
    maximumPrice: -1 as any
};   

export const searchReducer = handleActions<RootState.SearchState>(
    {
        [SearchActions.Type.SEARCH_STATUS]: (state, action) => {
            return {
                ...state,
                searchStatus: action.payload,
            }
        },
        [SearchActions.Type.SEARCH_CATEGORY]: (state, action) => {
            if(action.payload) {
                return {
                    ...state,
                    searchCategory: action.payload
                }
            } else {
                return state;
            }
            
        },
        [SearchActions.Type.SEARCH_KEYWORDS]: (state, action) => {
            if(action.payload) {
                return {
                    ...state,
                    searchText: action.payload
                }
            } else {
                return state;
            }
            
        },
        [SearchActions.Type.MINIMUM_PRICE]: (state, action) => {
            if(action.payload) {
                return {
                    ...state,
                    minimumPrice: action.payload
                }
            } else {
                return state;
            }
        },
        [SearchActions.Type.MAXIMUM_PRICE]: (state, action) => {
            if(action.payload) {
                return {
                    ...state,
                    maximumPrice: action.payload
                }
            } else {
                return state;
            }
        },
    }
    ,initialState)