import * as React from 'react';
import { Input } from 'reactstrap';
import Select from 'react-select';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  getValueLabelFromEnum, ShoppingCategories, SearchModel } from 'app/utils/consts';
import { Dispatch, bindActionCreators } from 'redux';
import { SearchActions } from 'app/actions';
import { omit } from 'app/utils';
import { connect } from 'react-redux';
// import { push } from 'connected-react-router'
// import { connect } from 'react-redux';


const styles = require('./style.css');

const logo = require('../../../assets/images/logo.png')

interface State {
    searchText: string,
    selectedCategory: any
}



namespace TopSearchMenu {

    export interface OwnProps {
        history: any
    }
    
    export interface DispatchProps {
        searchActions: SearchActions
    }
    
    export type Props = OwnProps & DispatchProps;
    
}



class _TopSearchMenu extends React.Component<TopSearchMenu.Props, State> {
    constructor(props: TopSearchMenu.Props) {
        super(props);
        this.state = {
            searchText: '',
            selectedCategory: getValueLabelFromEnum(ShoppingCategories)[0]
        }
    }


    handleSearchChange = (e: any) => {
        this.setState({
            searchText: e.target.value
        });
    }

    handleCategoryChanged = (selectedCategory: any) => {
        this.props.searchActions.changeSearchCategory(selectedCategory.label);
        this.setState({
            selectedCategory
        });
    }

    startSearch = () => {
        this.props.searchActions.changeSearchKeywords(this.state.searchText);
        this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_STARTED);
        this.props.history.push('/itemlist');
    }

    render() {
        return (
            <div className={styles.container}>
                <div className={styles.logoContainer}>
                    <img src={logo} className={styles.logo} />
                </div>
                <div className={styles.searchBox}>
                    <Input value={this.state.searchText} onChange={this.handleSearchChange} className={styles.inputSearch} type="text" placeholder="search product by name" />
                    <Select onChange={this.handleCategoryChanged} options={getValueLabelFromEnum(ShoppingCategories)} value={this.state.selectedCategory} className={styles.inputSelect} />
                    <div className={styles.searchIcon} onClick={this.startSearch}>
                        <FontAwesomeIcon icon="search" />
                    </div>                    
                </div>
                <div className={styles.moreDetails}>
                    <FontAwesomeIcon icon="heart"  className={styles.heartIcon} />
                    <FontAwesomeIcon icon="shopping-cart"  className={styles.cartIcon} />
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch: Dispatch): Pick<TopSearchMenu.Props, 'searchActions'> => ({
    searchActions: bindActionCreators(omit(SearchActions, 'Type'), dispatch)
});

export const TopSearchMenu = connect<{}, TopSearchMenu.DispatchProps, TopSearchMenu.Props>(null, mapDispatchToProps)(_TopSearchMenu as any);