import * as React from 'react';
import { ItemModel } from 'app/models/Items';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ImageGallery from 'react-image-gallery';
import Collapse from 'reactstrap/lib/Collapse';
// import Slider from 'react-slick';
// import Item from '../ItemList/Item';
import { getSingleItem, getRelatedItems } from 'app/utils/items';
import Slider from 'react-slick';
import Item from '../ItemList/Item';
// import { AppData } from 'app/utils/data';

const styles = require('./style.css');

interface State {
    selectedItemExtraDetails: ItemModel | null,
    images: any[] | null,
    isDescriptionOpen: boolean,
    relatedItemList: ItemModel[],
    isItemReady: boolean,
}

interface OwnProps {
    history: any
}

type Props = OwnProps;

export class ItemDetails extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            selectedItemExtraDetails: null,
            images: null,
            isDescriptionOpen: false,
            relatedItemList: [],
            isItemReady: false
        }
    }

    async componentDidMount() {

        this.setSelectedItemDetails();
    }

    async componentDidUpdate() {
        // if(this.state.selectedItem && this.state.selectedItem.id != this.props.history.location.pathname.split('/').pop() && this.state.isItemReady) {
        //     this.setState({
        //         isItemReady: false
        //     })
        //     window.scrollTo(0, 0);
        //     this.setSelectedItemDetails();
        // }
        
    }

    setSelectedItemDetails = async () => {
        const itemId = this.props.history.location.pathname.split('/')[3];
        const itemStore = this.props.history.location.pathname.split('/')[2];
        const singleItemResponse = await getSingleItem(itemStore, itemId)
        // if (singleItemResponse.status === 200) {
            const selectedItemExtraDetails = singleItemResponse;

            let images:any = [];
            selectedItemExtraDetails.morePictures.map((image: any) => {
                images.push({
                    original: image,
                    thumbnail: image,
                    thumbnailClass: styles.thumbnailClass, 
                    originalClass: styles.originalClass
                    // images.push({: image, thumbnail: image, thumbnailClass: styles.thumbnailClass, originalClass: styles.originalClass})
                })
            });

            const relatedItems = await getRelatedItems(itemId, itemStore);

            const [...relatedItemList] = await Promise.all(relatedItems.map((relatedItem: any) => getSingleItem(relatedItem.store, relatedItem.id)))

            this.setState({
                images,
                selectedItemExtraDetails,
                relatedItemList, 
            })
        // }
        
    }

    toggleDescription = () => {
        this.setState({
            isDescriptionOpen: !this.state.isDescriptionOpen
        })
    }

    handleOpenItemImage = () => {

    }

    setRelatedItems = async (itemId: number, itemStore: string) => {


        // const selectedItem = AppData.itemList.find((item: ItemModel) => item.id == itemId && item.store == itemStore)
        // const relatedItemsRequestList = selectedItem.relatedItems.map((relatedItem: any) => getSingleItem(relatedItem.store, relatedItem.id))
        // const [...response] = await Promise.all(relatedItemsRequestList)
        // // TODO - get single item for each...
        


        // return [];
    }


    render() {
        const settings = {
            dots: true,
            infinite: false,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 1
          };

        const fakeArray = [];
        const reviewAverate = this.state.selectedItemExtraDetails && this.state.selectedItemExtraDetails.reviewAverage || 0;
        for(let i=0; i<reviewAverate; i++) {
            fakeArray.push(1)
        }
        return (
            <>
            <div className={styles.itemDetailsContainer}>
                {this.state.selectedItemExtraDetails && 
                
                <div className={styles.item}>
                    <div>
                        <div className={styles.imageContainer}>
                            <div className={styles.image}>
                                <ImageGallery items={this.state.images} showPlayButton={false} stopPropagation={true} useBrowserFullscreen={true}
                                useTranslate3D={false} showBullets={true}
                                style={{maxHeight: '100vh', maxWidth: '100vw', objectFit: 'scale-down', fontFamily: "'object-fit: scale-down'"}}
                                />
                            </div>
                        </div>
                        <div className={styles.detailsContainer}>
                            <div className={styles.title}>
                                {this.state.selectedItemExtraDetails.title}
                            </div>
                            <div className={styles.reviews}>
                                <div className={styles.stars}>
                                    {fakeArray.map((value, index) => <FontAwesomeIcon key={index} icon="star" color={'#eded34'} className={styles.heartIcon} />)}
                                </div>
                                <div className={styles.reviewNumber}>
                                    ( {this.state.selectedItemExtraDetails.reviews && this.state.selectedItemExtraDetails.reviews.length} reviews )      
                                </div>
                            </div>
                            <div className={styles.price}>
                                <div className={styles.itemPrice}>
                                    {this.state.selectedItemExtraDetails.price}$
                                </div>
                                <div className={styles.shippingPrice}>
                                    (+{this.state.selectedItemExtraDetails.shippingPrice}$ shipping)
                                </div>
                                
                            </div>
                            <div className={styles.description}>
                                <div className={styles.header}>
                                    <div className={styles.text} onClick={this.toggleDescription}>
                                        Description 
                                        <FontAwesomeIcon icon={['fab', 'ebay']} />
                                    </div>
                                    <div className={styles.arrow} onClick={this.toggleDescription}>
                                        {!this.state.isDescriptionOpen ? 
                                        <FontAwesomeIcon icon="angle-down" />
                                        :
                                        <FontAwesomeIcon icon="angle-up" />
                                        }
                                    </div>
                                    
                                </div>
                                <div className={styles.body}>
                                <Collapse isOpen={this.state.isDescriptionOpen}>
                                    {this.state.selectedItemExtraDetails.description}
                                </Collapse>
                                </div>
                            </div>
                            <div className={styles.buyItNow}>
                                <div className={styles.button}>
                                    Buy it Now!
                                </div>
                            </div>
                        </div>
                        
                    </div>
                    {this.state.relatedItemList && this.state.relatedItemList.length > 0 &&
                    <div className={styles.similarItems}>
                        <div className={styles.header}>
                            <div className={styles.text}>
                                Related Items
                            </div>
                        </div>
                        <div className={styles.body}>
                        <Slider {...settings}>
                            {this.state.relatedItemList.map((relatedItem: ItemModel, index: number) => {
                                return (
                                    <div key={index}>
                                        <Item isRelatedItem={true} history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[index]} />
                                    </div>
                                )
                            })}
                            
                            {/* <div>
                                <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={this.state.relatedItemList[1]} />
                            </div> */}
                        </Slider>
                        </div>
                    </div>
                    }
                    
                </div>
                }
                
            </div>
            </>
        )
    }
}