import * as React from 'react';
import { RouteComponentProps, Switch, Route
  // , Switch, Route
 } from 'react-router';
import { NavigationTopMenu } from '../Header/NavigationTopMenu';
// import { RootState } from 'app/reducers';
import { connect } from 'react-redux';
import { bindActionCreators, AnyAction, Dispatch } from 'redux';

import { omit } from 'app/utils';
import { SearchActions } from 'app/actions/searchActions';
import {TopSearchMenu} from '../TopSearchMenu';
// import { MainApp } from '../MainApp';
import { ItemList } from '../ItemList';
import { ItemDetails } from '../ItemDetails';
import { RootState } from 'app/reducers/state';
import { SearchModel } from 'app/utils/consts';
import { MainApp } from '../MainApp';
// import { MainApp } from '../MainApp';
// import { TopBar } from '../TopBar';
// import { MainApp } from '../MainApp';
// import { LeftSideBar } from '../LeftSideBar';

const styles = require('./style.css');

export namespace App {
  export interface OwnProps extends RouteComponentProps<void> {
    // search: SearchModel.Status
    location: any,
    match: any
  }

  export interface StateProps {
    searchStatus: SearchModel,
    searchText: any,
    searchCategory: any
  }

  export type Props = OwnProps & StateProps;

}

const mapStateToProps = (state: RootState): Pick<App.StateProps, 'searchStatus' | 'searchText' | 'searchCategory'> => {
  return {searchStatus: state.search.searchStatus, searchText: state.search.searchText, searchCategory: state.search.searchCategory};
};

const mapDispatchToProps = (dispatch: Dispatch<AnyAction>) => ({
  actions: {
    otherActions: bindActionCreators(omit({...SearchActions}, 'Type'), dispatch)
  }
});

@connect(
  mapStateToProps,
  mapDispatchToProps
)
class _App extends React.Component<App.Props> {

  constructor(props: App.Props, context?: any) {
    super(props, context);
  }


  render() {
    const topSearchManuStyle = this.props.match.params.app !== undefined ? styles.topSearchMenu : styles.middleTopSearchBar;
    return (
      <div className={styles.appContainer}>
        <div className={styles.topNavigator}>
          <NavigationTopMenu />
        </div>
        <div className={topSearchManuStyle}>
          <TopSearchMenu history={this.props.history}  />
        </div>

        <Switch>
          <Route exact path="/app" render={(props) => <MainApp {...props} />} />
          <Route exact path="/itemList" render={(props) => <ItemList {...props} />} />
          <Route exact path="/item/:store/:id" render={(props) => <ItemDetails {...props} />} />
        </Switch>
{/* 
        <div className={styles.topBar}>
          <TopBar />
        </div>
        <div className={styles.siteBody}>
          site body
        </div> */}
      </div>
    );
  }
}

export const App = (_App) as any;