import * as React from 'react';
import Button from 'reactstrap/lib/Button';

interface SubmitButtonProps {
    content: string,
    onClick?: () => void
}

const SubmitButton = (props: SubmitButtonProps) => {
    return(
        <Button onClick={props.onClick}>
            {props.content}
        </Button>
    )
}

export default SubmitButton;