import { http } from "./http";

export const searchForItems = async (searchText: string, freeShippingOnly: string, pageNumber: string) => {
    return await http.get(`/api/v1/items/${searchText}/${freeShippingOnly}/${pageNumber}`);
}

export const getSingleItem = async (store: string, itemId: string) => {
    return await http.get(`/api/v1/items/getSingleItem/${store}/${itemId}`);
}

export const getItemURL = async (itemId: string, store: string) => {
    return await http.get(`/api/v1/db/getItemURL/${itemId}/${store}`);
}

export const getRelatedItems = async (itemId: string, store: string) => {
    return await http.get(`/api/v1/db/getRelatedItems/${itemId}/${store}`);
}

export const getSimilarItems = async (itemList: any[]) => {
    return await http.post(`/api/v1/items/setSimilarItems`, itemList);
}

export const saveRelatedItemsForItem = async (itemId: any, itemStore: string, relatedItems: {}, itemLink: string) => {
    const data = {
        itemId,
        itemStore,
        relatedItems,
        itemLink
    }
    return await http.post(`/api/v1/db/setRelatedItems`, data);
}