import * as React from 'react';
import {Categories} from './Categories';
import ListHeader from './ListHeader';
import Item from './Item';
import { RootState } from 'app/reducers/state';
import { connect } from 'react-redux';
import { SearchModel } from 'app/utils/consts';
import { SearchActions } from 'app/actions';
import { Dispatch, bindActionCreators } from 'redux';
import { omit } from 'app/utils';
import { ItemModel } from 'app/models/Items';
// import Lightbox from 'react-image-lightbox';
// import 'react-image-lightbox/style.css';
import { Lightbox } from "react-modal-image";
import {CircleArrow as ScrollUpButton} from "react-scroll-up-button";
import Loader from 'react-loader-spinner'
import { searchForItems, getSimilarItems, saveRelatedItemsForItem } from 'app/utils/items';
import { cloneDeep } from 'lodash';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { AppData } from 'app/utils/data';

const styles = require('./style.css');
// const yitemsURL = '../../../assets/mockups/items.json';

interface State {
    currentItemList: ItemModel[],
    isItemPictureOpen: boolean,
    selectedImageUrl: string,
    imageModalTitle: string,
    itemsPerPage: number,
    minimumPrice: number,
    maximumPrice: number,
    originalItemsLength: number
}

interface OwnProps {    
    history: any
}

interface DispatchProps {
    searchActions: SearchActions
}

interface StateProps {
    searchStatus: SearchModel,
    searchText: any,
    minimumPrice: any,
    maximumPrice: any,
    
}

type Props = OwnProps & StateProps & DispatchProps;

class _ItemList extends React.Component<Props, State> {

    itemList: any[] = [];

    constructor(props: Props) {
        super(props);
        this.state = {
            currentItemList: [],
            isItemPictureOpen: false,
            selectedImageUrl: '',
            imageModalTitle: '',
            itemsPerPage: 8,
            minimumPrice: 0,
            maximumPrice: 0,
            originalItemsLength: 0
        }
    }

    async componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
        this.getSearchItems();
    }

    componentDidUpdate(prevProps: Props) {
        if (prevProps.searchStatus !== this.props.searchStatus &&  this.props.searchStatus === SearchModel.SEARCH_STARTED) {
            this.getSearchItems();
        }

        if (prevProps.minimumPrice !== this.props.minimumPrice || prevProps.maximumPrice !== this.props.maximumPrice) {
            let items: ItemModel[] = cloneDeep(this.itemList);
            items = items.filter((item: ItemModel) => item.price <= this.props.maximumPrice &&  item.price >= this.props.minimumPrice)
            this.setState({currentItemList: items})
        }
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    setDatabaseRelatedItems = () => {
        this.itemList.map((item: ItemModel) => {
            if (item.relatedItems && item.relatedItems.length > 0) {
                saveRelatedItemsForItem(item.id, item.store, item.relatedItems, item.itemURL);
            }
        })
    }

    getSearchItems = async () => {
        const freeShippingOnly = 'true';
        this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_RETRIVING_ITEMS);
        const [...response] = await Promise.all([
            searchForItems(this.props.searchText, freeShippingOnly, "1"), 
            searchForItems(this.props.searchText, freeShippingOnly, "2"),
            searchForItems(this.props.searchText, freeShippingOnly, "3"),
            searchForItems(this.props.searchText, freeShippingOnly, "4"),
            searchForItems(this.props.searchText, freeShippingOnly, "5"),
            searchForItems(this.props.searchText, freeShippingOnly, "6"),
            searchForItems(this.props.searchText, freeShippingOnly, "7"),
            searchForItems(this.props.searchText, freeShippingOnly, "8"),
            searchForItems(this.props.searchText, freeShippingOnly, "9"),
            searchForItems(this.props.searchText, freeShippingOnly, "10")])
        this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_ORDERING_ITEMS)
        let items: any = [];
        if (response.length <= 0) {
            return;
        } else {
            response.map((itemsResponse: any) => {
                // if (itemsResponse.status === 200) {
                    items = items.concat(itemsResponse)
                // } else {
                //     this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_FAILED);
                //     return;
                // }
            })
        }
        this.itemList = [];
        const similarItemsResponse = await getSimilarItems(items);
        // if (similarItemsResponse.status === 200 ) {
            similarItemsResponse.data.map((item: any) => {
                if (item.isItemBest) {
                    this.itemList.push(item);
                }
            })
            AppData.itemList = this.itemList;
            this.setState({
                currentItemList: this.itemList,
                maximumPrice: Math.max(...this.itemList.map((item) => item.price)),
                minimumPrice: Math.min(...this.itemList.map((item) => item.price)),
                originalItemsLength: items.length
            });
            this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_SUCCESS);
        // } else {
        //     this.props.searchActions.changeSearchStatus(SearchModel.SEARCH_FAILED);
        //     return;
        // }

        this.setDatabaseRelatedItems();
    }

    handleOpenItemImage = (itemId: number) => {
        const selectedItem = this.state.currentItemList.find((item: ItemModel) => item.id === itemId);
        if(selectedItem) {
            this.setState({
                selectedImageUrl: selectedItem.bigImageUrl,
                isItemPictureOpen: true,
                imageModalTitle: selectedItem.title,
                
            });
        }
    }

    closeImageModal = () => {
        this.setState({
            isItemPictureOpen: false
        })
    }

    handleFilters = (item: ItemModel) => {
        if(this.props.maximumPrice !== -1 && this.props.minimumPrice !== -1) {
            return item.price <= this.props.maximumPrice && item.price >= this.props.minimumPrice
        } else {
            return true;
        }
        
    }

    handleScroll = () => {
        const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
        const body = document.body;
        const html = document.documentElement;
        const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
        const windowBottom = Math.round(windowHeight + window.pageYOffset);
        if (windowBottom >= docHeight - 10) {
            this.setState({
                itemsPerPage: this.state.itemsPerPage + 4 
            }); 
        }
    }

    render() {

        const isSearchInProgress = this.props.searchStatus === SearchModel.SEARCH_ORDERING_ITEMS || this.props.searchStatus === SearchModel.SEARCH_RETRIVING_ITEMS ||
        this.props.searchStatus === SearchModel.SEARCH_STARTED;

        return (
            <>
                 {this.state.isItemPictureOpen && (
                     <Lightbox
                     medium={this.state.selectedImageUrl}
                     large={this.state.selectedImageUrl}
                     alt={this.state.imageModalTitle}
                     onClose={this.closeImageModal}
                   />
                )}
                <ScrollUpButton />
                {isSearchInProgress &&
                <div className={styles.searching}>
                    <div className={styles.searchSpinner}>
                        <Loader 
                            type="TailSpin"
                            color="#ff3c20"
                            height="50"	
                            width="50"
                        />  
                    </div>
                    <div className={styles.searchDetails}>
                        Getting items 
                        {this.props.searchStatus === SearchModel.SEARCH_ORDERING_ITEMS && <FontAwesomeIcon icon="check-circle" />}
                        {this.props.searchStatus === SearchModel.SEARCH_ORDERING_ITEMS && 
                            <div>
                                Ordering items, please wait...
                            </div>
                        }
                    </div>
                     
                </div>}
                {this.props.searchStatus === SearchModel.SEARCH_FAILED &&
                <div>
                    Search failed
                </div>}
                {this.props.searchStatus === SearchModel.SEARCH_SUCCESS && 
                    <div className={styles.itemListContainer}>  
                        <div className={styles.categories}>
                            <Categories maximumPrice={this.state.maximumPrice} minimumPrice={this.state.minimumPrice} />
                        </div>
                        <div className={styles.items}>
                            <div className={styles.listHeader}>
                                <ListHeader originalItemsLength={this.state.originalItemsLength} totalItems={this.state.currentItemList.length} minimumPrice={this.props.minimumPrice} maximumPrice={this.props.maximumPrice} />
                            </div>
                            <div className={styles.ItemList}>
                                {this.state.currentItemList.map((item: ItemModel, index) => {
                                    if(this.handleFilters(item) && index < this.state.itemsPerPage) {
                                        return (
                                            <Item history={this.props.history} onItemImageSelected={this.handleOpenItemImage} item={item} key={index} />
                                        )
                                    } else {
                                        return;
                                    }
                                    
                                })}
                            </div>
                        </div>
                    </div>
                }
            </>
        )
    }
}

const mapStateToProps = (state: RootState): Pick<StateProps, 'searchStatus' | 'searchText' | 'minimumPrice' | 'maximumPrice'> => {
    return {searchStatus: state.search.searchStatus, searchText: state.search.searchText, minimumPrice: state.search.minimumPrice, maximumPrice: state.search.maximumPrice};
  };

const mapDispatchToProps = (dispatch: Dispatch): Pick<Props, 'searchActions'> => ({
    searchActions: bindActionCreators(omit(SearchActions, 'Type'), dispatch)
});

  
  export const ItemList = connect<StateProps, DispatchProps, Props>(mapStateToProps, mapDispatchToProps)(_ItemList as any);