import * as React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { ItemModel } from 'app/models/Items';
import { AppData } from 'app/utils/data';
import ReactTooltip from 'react-tooltip'



const styles = require('./style.css');

interface State {

}

interface OwnProps {
    item: ItemModel,
    onItemImageSelected: (itemId: number) => void;
    history: any;
    isRelatedItem?: boolean;
}

type Props = OwnProps;

class Item extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {

        }
    }

    openBigImage = (itemId: number) => {
        return () => {
            this.props.onItemImageSelected(itemId);
        }
    }

    handleGetItem = () => {
        AppData.selectedItem = this.props.item;
        this.props.history.push(`/item/${this.props.item.store}/${this.props.item.id}`);
    }

    render() {
        // const fakeArray = [];
        // for(let i=0; i<this.props.item.reviewAverage; i++) {
        //     fakeArray.push(1)
        // }
        const itemImage = this.props.isRelatedItem ? this.props.item.bigImageUrl : this.props.item.smallImageUrl;
        const imageStyle = this.props.isRelatedItem ? {height: "100%", margin: "auto"} : {};
        return (
            <>
            <div className={styles.gridItemContainer}>
                <div className={styles.gridItemHeader}>
                    {this.props.item.condition}
                </div>
                <div className={styles.gridImageContainer}>
                    <img src={`${itemImage}`} style={imageStyle} />
                </div>
                <div className={styles.gridItemTitle} data-tip={this.props.item.title}>
                    {this.props.item.title}
                </div>
                <ReactTooltip />
                <div className={styles.gridItemPrice}>
                    <span className={styles.gridItemPriceMain}>{this.props.item.price}$</span> 
                    <span className={styles.gridItemPriceShipping}>( + {this.props.item.shippingPrice}$ Shipping )</span>
                </div>
                <div className={styles.gridItemRating}>
                    <div className={styles.gridItemRatingStart}>
                        {/* {fakeArray.map((value, index) => <FontAwesomeIcon key={index} icon="star" color={'#eded34'} className={styles.heartIcon} />)} */}
                    </div>
                    <div className={styles.gridItemRatingDetails}>
                        ({this.props.item.reviews && this.props.item.reviews.length} reviews)
                        {this.props.item.isItemBest && <><br /><span>best</span></>}
                        <br />
                        similar items: {this.props.item.relatedItems && this.props.item.relatedItems.length}
                    </div>
                    <div className={styles.gridItemMoreDetails}>
                        <div className={styles.gridItemMoreDetailsOpenItemPicture} onClick={this.openBigImage(this.props.item.id)}>
                            <FontAwesomeIcon icon="search-plus" color={'#a5a5a5'} size={'lg'} className={styles.heartIcon} />
                        </div>
                        <div className={styles.gridItemMoreDetailsOpenItem} onClick={this.handleGetItem}>
                            Get Item!
                        </div>
                    </div>
                </div>
            </div>
            </>
        )
    }
}

export default Item;