import "react-input-range/lib/css/index.css";
import * as React from 'react';
import InputRange from 'react-input-range';
import SubmitButton from "app/components/SubmitButton";
import { SearchActions } from "app/actions";
import { bindActionCreators, Dispatch } from "redux";
import { omit } from "app/utils";
import { connect } from "react-redux";


const styles = require('./style.css');

interface State {
    priceValue: any
}

interface OwnProps {
    minimumPrice: number,
    maximumPrice: number,
}

interface DispatchProps {
    searchActions: SearchActions
}

type Props = OwnProps & DispatchProps;

class _Categories extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            priceValue: {
                min: this.props.maximumPrice/2 - this.props.maximumPrice/4,
                max: this.props.maximumPrice/2 + this.props.maximumPrice/4
            }
        }
    }

    handlePriceFilter = () => {
        this.props.searchActions.changeMinimumPrice(this.state.priceValue.min);
        this.props.searchActions.changeMaximumPrice(this.state.priceValue.max);
    }

    render() {
        return (
            <>
            <div className={styles.categoriesContainer}>
                <div className={styles.header}>
                    Filter By Price
                </div>
                <div className={styles.priceFilterContainer}>
                    <div className={styles.priceFilterRange}>
                        <InputRange
                            draggableTrack
                            maxValue={this.props.maximumPrice}
                            minValue={this.props.minimumPrice}
                            onChange={value => this.setState({ priceValue: value as number })}
                            onChangeComplete={value => console.log(value)}
                            value={this.state.priceValue} />
                    </div>
                    <div className={styles.priceFilterSubmit}>
                        <div className={styles.priceFilterText}>
                            Price: 
                            <span className={styles.priceColor}>
                                {this.state.priceValue.min}$ to {this.state.priceValue.max}$
                            </span>
                        </div>
                        <div className={styles.priceFilterButtonContainer}>
                            <SubmitButton content={`Filter!`} onClick={this.handlePriceFilter} />
                        </div>
                    </div>
                </div>
            </div>
            </>
        )
    }
}

const mapDispatchToProps = (dispatch: Dispatch): Pick<Props, 'searchActions'> => ({
    searchActions: bindActionCreators(omit(SearchActions, 'Type'), dispatch)
});


export const Categories = connect<{}, DispatchProps, Props>(null, mapDispatchToProps)(_Categories as any);
