import { createAction } from "redux-actions";

export namespace SearchActions {
    export enum Type {
        SEARCH_STATUS = 'SEARCH_STATUS',
        SEARCH_CATEGORY = 'SEARCH_CATEGORY',
        SEARCH_KEYWORDS = 'SEARCH_KEYWORDS',
        MINIMUM_PRICE = 'MINIMUM_PRICE',
        MAXIMUM_PRICE = 'MAXIMUM_PRICE'
    }
    export const changeSearchStatus = createAction(Type.SEARCH_STATUS);
    export const changeSearchCategory = createAction(Type.SEARCH_CATEGORY);
    export const changeSearchKeywords = createAction(Type.SEARCH_KEYWORDS);
    export const changeMinimumPrice = createAction(Type.MINIMUM_PRICE);
    export const changeMaximumPrice = createAction(Type.MAXIMUM_PRICE);
}
export type SearchActions = Omit<typeof SearchActions, 'Type'>;
