import * as React from 'react';

const styles = require('./style.css');

interface State {
    
}

interface OwnProps {
    totalItems: number,
    minimumPrice: any,
    maximumPrice: any,
    originalItemsLength: number
}

type Props = OwnProps;

class ListHeader extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {

        }
    }
    render() {
        return (
            <div className={styles.listHeaderContainer}>
                <div className={styles.totalItems}>
                    Filtered items: {this.props.totalItems},
                    out of total {this.props.originalItemsLength}
                </div>
                <div className={styles.sortBy}>
                    minimum price: {this.props.minimumPrice}, maximumPrice: {this.props.maximumPrice}
                </div>
            </div>
        )
    }
}

export default ListHeader;